
import { mapMutations } from 'vuex'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapState, mapActions, mapGetters } from 'vuex'

export default {

  components: {
    Swipeout,
    SwipeoutItem,
    SwipeoutButton,
    Cell
  },

  computed: {
    ...mapGetters([
      "selectedCities"
    ]),
  },

  mounted() {
    this.SET_TITLE(this.$t('Cities'))
    this.SET_RIGHT_OPTION({ show: false })
  },

  methods: {
    ...mapMutations(['SET_TITLE', 'SET_RIGHT_OPTION']),
    ...mapActions([
      'changeCity',
      'deleteCity',
    ]),
  }
}




